import React, { PureComponent } from 'react'
import merge from 'lodash/merge'

import AnswerContext from './AnswerContext'

const STORAGE_KEY = 'RE_HEALTH_SURVEY'

export default class AnswerProvider extends PureComponent {
  state = {
    answers: JSON.parse(window.localStorage.getItem(STORAGE_KEY)) || []
  }

  setAnswer = (values) => {
    this.setState(({ answers }) => {
      return {
        answers: merge(answers, values.a),
      }
    }, () => {
      window.localStorage.setItem(STORAGE_KEY, JSON.stringify(this.state.answers))
    })
  }

  clearAnswer = () => {
    window.localStorage.removeItem(STORAGE_KEY)
  }

  render() {
    const { answers } = this.state
    return (
      <AnswerContext.Provider value={{
        answers,
        setAnswer: this.setAnswer,
        clearAnswer: this.clearAnswer,
      }}>
        {this.props.children}
      </AnswerContext.Provider>
    )
  }
}
